<template>
    <div class="calc-container">
        <h1>Calculadora IMC</h1>

        <form @submit="calcIMC">
            <div class="input-box">
                <label for="weight">Peso em kg</label>
                <input type="number" v-model="weight" >
            </div>

            <div class="input-box">
                <label for="height">Altura em metros</label>
                <input type="text" v-model="height" >
            </div>

            <button>Calcular</button>
        </form>

        <div class="result">
            <h2>{{ imc }}</h2>
            <div>
                <p>Seu IMC</p>
                <p class="msg">{{ msg }}</p>
            </div>
        </div>
    </div>
</template>


<script>
    export default {
        name: 'CalcContainer',

        data() {
            return {
                height: '',
                weight: '',
                imc: '',
                msg: ''
            }
        },

        methods: {
            calcIMC(e) {
                e.preventDefault()
                this.imc = (Number(this.weight) / (this.height * this.height)).toFixed(2)
                this.verifyIMC()
            },

            verifyIMC() {
                    if (this.imc <= 18.5) {
                        this.msg = 'Você está abaixo do peso ideal'
                    }

                    if (this.imc >= 18.5 && this.imc <= 24.9) {
                        this.msg = 'Você está no peso ideal'
                    }

                    if (this.imc >= 25 && this.imc <= 29.9) {
                        this.msg = 'Você está acima do peso ideal'
                    }

                    if (this.imc >= 30) {
                        this.msg = 'Você está obeso'
                    }
                
                    return this.msg
                }
        }
    }
</script>


<style scoped>
    .calc-container {
        color: #fafafa;
        display: flex;
        flex-direction: column;
    }

    h1 {
        margin-bottom: 10px;
    }

    .input-box {
        margin: 15px 0;
    }

    input {
        background-color: rgba(255,255,255,.23);
        color: #fafafa;
        padding: 7px 15px;
        border: none;
        outline: none;
        border-radius: 7px;
        font-size: 1.3rem;
        margin-top: 10px;
    }

    input::-webkit-inner-spin-button {
        appearance: none;
    }

    button {
        border: none;
        border-radius: 7px;
        background-color: rgba(255,255,255,.4);
        color: #fafafa;
        padding: 7px;
        font-size: 1.3rem;
        justify-self: center;
        width: 100%;
        margin-top: 10px;
        transition: .3s;
        cursor: pointer;
    }

    button:hover {
        background-color: rgba(255,255,255,.3);
    }

    .result {
        margin-top: 25px;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .result h2 {
        font-size: 2.3rem;
    }

    .result p {
        font-size: 1.3rem;
    }

    .result div p:first-child {
        font-size: .8rem;
    }

    .result div {
        display: flex;
        gap: 15px;
    }

    .msg {
    }


    @media screen and (max-width: 700px) {
        input {
            width: 100%;
        }
        .calc-container {
            min-width: 350px;
            padding: 25px;
        }
    }

</style>